<template>
  <div id="app">

    <div class="container">
      <div class="row">
        <div class="col-12 mt-2">
          <VueSignaturePad
            id="signature"
            width="100%"
            height="500px"
            ref="signaturePad"
            :options="options"
          />
        </div>
      </div>
      <div class="row">
        <div class="col-3 mt-2">    
        </div>

      
          <div class="col-3 mt-2">
            <button style="background-color: orange; position: relative; height: 70px; width: 150px; padding: 15px 30px; margin: 10px;border: none; text-align: center;  text-decoration: none; font-size: 22px; cursor: pointer; overflow: hidden; -webkit-transition-duration: 0.4s; transition-duration: 0.4s;" class="btn btn-outline-primary" @click="save">
            
               
                    Save
              
            </button>
          </div>
       
        <div class="col-3 mt-2">
        </div>
        <div class="col-3 mt-2">
         
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import 'url-search-params-polyfill';
import axios from 'axios';
export default {
  name: "App",
  data: () => ({
    options: {
      penColor: "#c0f"
    },
    data:"",
    isEmpty:"",


  }),
  methods: {
    undo() {
      this.$refs.signaturePad.undoSignature();
    },
    save() {
      const { isEmpty, data } = this.$refs.signaturePad.saveSignature();
      

      console.log(isEmpty);
      console.log(typeof(data));
      console.log(data);  //canvas DATA
      var DD='123454565153'
 

      const fd = new URLSearchParams()
      fd.append('data', data) //canvas DATA
      fd.append('isEmpty',isEmpty) //비어있는 값인지 확인
//http://mesidbtest.cafe24app.com
      axios.post('http://192.1.1.30:3000/ind/canvas', fd)
        .then((req, res) => {
         alert('서명 보내기 성공')
        
          console.log(res)
        })
        .catch(e => {
         alert('실패')
        })

        
  
     
    },
    change() {
      this.options = {
        penColor: "#00f"
      };
    },
    resume() {
      this.options = {
        penColor: "#c0f"
      };
    }
  }
};
</script>

<style>
#signature {
  border: double 3px transparent;
  border-radius: 5px;
  background-image: linear-gradient(white, white),
  radial-gradient(circle at top left, #4bc5e8, #9f6274);
  background-origin: border-box;
  background-clip: content-box, border-box;
}
</style>
